/* eslint-disable no-console */
import { BASE_URL } from "../../../config"

export default {
    name: "DashboardLayout",
    data() {
        return {
            drawer: false,
            config: '',
            miniDrawState: true,
            dashActive: true,
            userFormActive: false
        }
    },
    mounted: function () {
        if(this.$router.currentRoute.name == 'Dashboard'){
            this.dashActive = true
        }else if (this.$router.currentRoute.name == 'UserForm'){
            this.userFormActive = true
            this.dashActive = false
        }

        this.config = {
            headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('access_token')
            }
        }
    },
    methods: {
        logout() {
            this.axios.post(`${BASE_URL}/logout`, {}, this.config)
            .then(res => {
                if(res.data.status) {
                    this.$q.notify({
                        position: 'top',
                        color: 'blue-grey-7',
                        message: 'Se cerró la sesión',
                        timeout: 2000,
                    })
                }
            })

            localStorage.removeItem('access_token')
            this.$router.push({name:'Login'})
        },
        route(name) {
            if((name != 'Dashboard' && this.dashActive) || (name != 'UserForm' && this.userFormActive)){
                if(name == 'Dashboard'){
                    this.dashActive = true
                    this.userFormActive = false
                }else if(name == 'UserForm'){
                    this.dashActive = false
                    this.userFormActive = true
                }

                this.$router.push({name: name})
            }
        }
    }
}